
import { criticalActions } from "./crit-actions.js"

import { CHECKLIST_META_DATA as CMDV0, INTV_META_DATA as IMDV0 } from "./traumaScenarioV0MetaData.js"
import { CHECKLIST_META_DATA as CMDV2, INTV_META_DATA as IMDV2 } from "./traumaScenarioV2MetaData.js"
import { INTV_FORMS } from "./traumaScenarioV2.js"

export const getMetaData = async (db, scenarioType, schemaVersion) => {
    if ("trauma" !== scenarioType) {
        throw(`ERROR: scenario type: ${scenarioType} isn't supported`)
    }
    switch (schemaVersion) {
        case "V0":
            return { "CHECKLIST_META_DATA": CMDV0, "INTV_META_DATA": IMDV0 }
        case "V2":
            return { "CHECKLIST_META_DATA": CMDV2, "INTV_META_DATA": IMDV2 }
        default:
            return Promise.reject(new Error(`ERROR: unknown schema version: ${schemaVersion}`))
    }
}

export const getInterventionForms = async (db, scenarioType, schemaVersion) => {
    if ("trauma" !== scenarioType) {
        return Promise.reject(new Error(`ERROR: scenario type: ${scenarioType} isn't supported`))
    }
    if (schemaVersion !== "V2") {
        return {}
    } else {
        return INTV_FORMS
    }
}

export const getCriticalActions = async (db, scenario, schemaVersion) => {
    if (scenario !== "PHTC M2CA") {
        return Promise.reject(new Error(`ERROR: scenario ${scenario} not found`))
    }
    if (schemaVersion !== "V0") {
        return Promise.reject(new Error(`ERROR: currently no critical actions for schema version ${schemaVersion}`))
    }
    return criticalActions
}

