import React from "react"

import {
    HashRouter as Router,
    Switch,
    Route
} from "react-router-dom"

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth"

import "bootstrap/dist/css/bootstrap.min.css";

import "./styles.css";

import RouteComponent from "./routes/RouteComponent"

import DebriefingLog from "./components/DebriefingLog"

import UnauthedLayout from "./components/UnauthedLayout"

const App = (props) => {

    return (
        <AuthProvider>
          <Router>
            <Switch>
              <Route exact path="/test" component={DebriefingLog} />
              <PrivateRoute
                  exact path="/scenario/:scenarioId"
                  authFlowLayout={UnauthedLayout}
                  component={RouteComponent}
              />
              {/*default (catchall) route*/}
              <PrivateRoute
                  authFlowLayout={UnauthedLayout}
                  component={RouteComponent}
              />
            </Switch>
          </Router>
        </AuthProvider>
    )
}


export default App;