import React, { useState, useEffect } from 'react';

import { Button, Input, Table } from 'reactstrap';

import { formatAsMinSecs } from "../utils"

import { getCollection,
    getDocument,
    // publishDocument,
    // verifyDocument,
    // deleteDocument,
    listDocumentNames,
    retrieveCriticalActions,
    retrieveMetaData
}
from '../scripts/connect-firebase.js';

import Header from './Header';

import classnames from "classnames";

const colorTypes = {
    "Good": "text-success",
    "Caution": "text-warning",
    "Problem": "text-danger",
    "Not implemented": "text-muted"
}

const iconTypes = {
    "Good": "sentiment_satisfied",
    "Caution": "report_problem",
    "Problem": "highlight_off",
    "Not implemented": "exposure_neg_1"
}

const getAssessmentIcon = (currentEntry) => {
    switch (currentEntry.status) {
        case "contraindicated":
            return iconTypes["Problem"];
        case "missingMinimal":
            return iconTypes["Problem"];
        case "missingStandard":
            return iconTypes["Caution"];
        case "missingAssessment":
            return iconTypes["Problem"];
        case "assessment":
            return iconTypes["Good"];
        case "standard":
            return iconTypes["Good"];
        case "minimal":
            return iconTypes["Good"];
        case "notFound":
            return iconTypes["Not implemented"];
        default:
            return iconTypes["Not implemented"];
    }
}

const getColor = (currentEntry) => {
    switch (currentEntry.status) {
        case "contraindicated":
            return colorTypes["Problem"];
        case "missingMinimal":
            return colorTypes["Problem"];
        case "missingStandard":
            return colorTypes["Caution"];
        case "missingAssessment":
            return colorTypes["Problem"];
        case "assessment":
            return colorTypes["Good"];
        case "standard":
            return colorTypes["Good"];
        case "minimal":
            return colorTypes["Good"];
        case "green":
            return colorTypes["Good"];
        case "yellow":
            return colorTypes["Caution"];
        case "red":
            return colorTypes["Problem"];
        case "notFound":
            return colorTypes["Not implemented"];
        default:
            return colorTypes["Not implemented"];
    }
}

const getFeedback = (currentEntry) => {
    switch (currentEntry.status) {
        case "contraindicated":
            return currentEntry.contraindicatedWhy;
        case "missingMinimal":
            return currentEntry.minimalWhy;
        case "missingStandard":
            return "This is recommended in protocol " + currentEntry.protocol;
        case "missingAssessment":
            return "Missing assessment";
        case "assessment":
            return "";
        case "standard":
            return "";
        case "minimal":
            return "";
        case "notFound":
            return currentEntry.status + ": " + currentEntry.label + " (" + currentEntry.id + ")";
        default:
            return currentEntry.status + ": " + currentEntry.label + " (" + currentEntry.id + ")";
    }
}

const getTimestamp = (currentEntry) => {
    if (currentEntry.status === "criticalActionHeader") {
        return "";
    }
    if (currentEntry.timestamp === -1) {
        return "N/A";  // formatAsMinSecs would convert this to 59:59
    }
    return formatAsMinSecs(currentEntry.timestamp);
}

const DebriefingLog = () => {
    // const { scenario } = useContext(ScenarioContext)
    const [showCommentColumn, setShowCommentColumn] = useState(false);
    const [documentID, setDocumentID] = useState("9h4VwpOT6Jj66VpKGAmm");  // TODO: Have this set to null by default. Currently, doing this causes the buttons to select a log to be hidden until clicking another button.
    const [logs, setLogs] = useState(null);
    const [logsList, setLogsList] = useState(null);
    const [currentDocument, setCurrentDocument] = useState(null);
    const [metaData, setMetaData] = useState(null);
    const [criticalActions, setCriticalActions] = useState(null);
    const [finishedPerformedActions, setFinishedPerformedActions] = useState(false);  // Set to true once all performed actions have been displayed in the log
    const [confirmedActions, setConfirmedActions] = useState([]);
    const [formattedActions, setFormattedActions] = useState(null);



    const toggleCommentColumn = () => {
        setShowCommentColumn(!showCommentColumn);
    }


    const getActionDescription = (id, performed=true) => {
        if (metaData == null || metaData == undefined) {
            return (id + " not found");
        }
        let metaDataEntry = metaData.find((rec) => rec.id === id);
        if (metaDataEntry == null || metaDataEntry == undefined) {
            return (id + " not found");
        }
        let description = metaDataEntry.label;
        if (!performed) {
            description = "Did not " + description;
        }
        return description;
    }



    const showDocumentList = () => {
        for (const [key, value] of Object.entries(logsList)) {
            console.log(key, value);
        }
    }

    const getMetaDataEntry = (id) => {
        if (metaData == null) {
            return null;
        }
        return metaData.find((rec) => rec.id === id);
    }

    const getCriticalActionsInformation = (id) => {
        for (let i = 0; i < criticalActions["criticalActions"].length; i++) {
            for (let j = 0; j < criticalActions["criticalActions"][i]["contraindicated"].length; j++) {
                console.log(criticalActions["criticalActions"][i]["contraindicated"][j])
            }
        }
    }


    useEffect(() => {

        // Retrieve the metadata and critical actions before parsing the log file
        retrieveMetaData("trauma", "V0").then((metaDataResult) => {
            setMetaData(metaDataResult["CHECKLIST_META_DATA"].concat(metaDataResult["INTV_META_DATA"]))
            let tempMetaData = metaDataResult["CHECKLIST_META_DATA"].concat(metaDataResult["INTV_META_DATA"]);

            retrieveCriticalActions("PHTC M2CA", "V0").then((criticalActionsResult) => {  // TODO: Modify this to handle other scenarios once they are available
                setCriticalActions(criticalActionsResult);

                documentID ?
                getDocument("logs", documentID)
                .then((result) => {
                    setCurrentDocument(result);
                    setConfirmedActions(result["events"].map(entry => entry.id));  // Stores an array of id strings
                    let tempFormattedActions = [];

                    // For each entry in the log file
                    for (let i = 0; i < result["events"].length; i++) {
                        // Gets the action description
                        let tempActionDescription = "N/A";
                        let tempStatus = "missingFromCriticalActions";
                        let tempLabel = "Missing from crit-actions.js"
                        let metaDataEntry = tempMetaData.find((rec) => rec.id === result["events"][i].id);  // Searches for the current item in the metadata
                        if (metaDataEntry == null || metaDataEntry == undefined) {
                            tempActionDescription = result["events"][i].id + " not found";
                            tempStatus = "notFound";
                            tempLabel = "notFound";
                        } else {
                            tempActionDescription = metaDataEntry.label;
                            if (metaDataEntry.type === "assessment") {
                                tempStatus = "assessment";
                                tempLabel = "Assessment";
                            }
                        }

                        // Each tempObject is one of the lines in the displayed log
                        let tempObject = {
                            "id": result["events"][i].id,
                            "numericalID": i,  // The number displayed in the "ID" column
                            "status": tempStatus,
                            "contraindicatedWhy": "N/A",  // Feedback
                            "minimalWhy": "N/A",  // Feedback
                            "label": tempLabel,
                            "protocol": "N/A",
                            "actionDescription": tempActionDescription,
                            "timestamp": result["events"][i].timestamp
                        }

                        // Searches through criticalActions to find out whether any of these are standard interventions
                        for (let j = 0; j < criticalActionsResult["criticalActions"].length; j++) {
                            for (let k = 0; k < criticalActionsResult["criticalActions"][j]["standard"].length; k++) {
                                if (criticalActionsResult["criticalActions"][j]["standard"][k] === result["events"][i].id) {  // If the item is found in one of the arrays of standard actions
                                    tempObject["status"] = "standard";
                                    tempObject["label"] = criticalActionsResult["criticalActions"][j]["label"];
                                    tempObject["protocol"] = criticalActionsResult["criticalActions"][j]["protocol"];
                                }
                            }
                        }

                        // Searches through criticalActions to find out whether any of these are minimal interventions
                        for (let j = 0; j < criticalActionsResult["criticalActions"].length; j++) {
                            for (let k = 0; k < criticalActionsResult["criticalActions"][j]["minimal"].length; k++) {
                                if (criticalActionsResult["criticalActions"][j]["minimal"][k] === result["events"][i].id) {  // If the item is found in one of the arrays of minimal actions
                                    tempObject["status"] = "minimal";
                                    tempObject["label"] = criticalActionsResult["criticalActions"][j]["label"];
                                    tempObject["protocol"] = criticalActionsResult["criticalActions"][j]["protocol"];
                                }
                            }
                        }

                        // Searches through criticalActions to find out whether any of these are contraindicated
                        for (let j = 0; j < criticalActionsResult["criticalActions"].length; j++) {
                            for (let k = 0; k < criticalActionsResult["criticalActions"][j]["contraindicated"].length; k++) {
                                if (criticalActionsResult["criticalActions"][j]["contraindicated"][k] === result["events"][i].id) {  // If the item is found in one of the arrays of contraindicated actions
                                    tempObject["status"] = "contraindicated";
                                    tempObject["contraindicatedWhy"] = criticalActionsResult["criticalActions"][j]["contraindicatedWhy"];
                                    tempObject["label"] = criticalActionsResult["criticalActions"][j]["label"];
                                    tempObject["protocol"] = criticalActionsResult["criticalActions"][j]["protocol"];
                                }
                            }
                        }

                        tempFormattedActions.push(tempObject);
                    }

                    let tempConfirmedActions = result["events"].map(entry => entry.id);  // Copied here so each missing item can be tracked to ensure it isn't displayed twice
                    let indexCounter = result["events"].length;

                    // Searches through criticalActions to find out whether any of the minimal interventions are missing
                    for (let j = 0; j < criticalActionsResult["criticalActions"].length; j++) {
                        for (let k = 0; k < criticalActionsResult["criticalActions"][j]["minimal"].length; k++) {
                            if (!tempConfirmedActions.includes(criticalActionsResult["criticalActions"][j]["minimal"][k])) {  // If one of the minimal items is missing
                                let tempActionDescription = "N/A";
                                let metaDataEntry = tempMetaData.find((rec) => rec.id === criticalActionsResult["criticalActions"][j]["minimal"][k]);
                                if (metaDataEntry == null || metaDataEntry == undefined) {
                                    tempActionDescription = criticalActionsResult["criticalActions"][j]["minimal"][k] + " not found";
                                } else {
                                    tempActionDescription = "Did not " + metaDataEntry.label;
                                }
                                let tempObject = {
                                    "id": criticalActionsResult["criticalActions"][j]["minimal"][k],
                                    "numericalID": indexCounter,
                                    "status": "missingMinimal",
                                    "contraindicatedWhy": "N/A",
                                    "minimalWhy": criticalActionsResult["criticalActions"][j]["minimalWhy"],
                                    "label": criticalActionsResult["criticalActions"][j]["label"],
                                    "protocol": criticalActionsResult["criticalActions"][j]["protocol"],
                                    "actionDescription": tempActionDescription,
                                    "timestamp": -1
                                }
                                indexCounter++;
                                tempConfirmedActions.push(criticalActionsResult["criticalActions"][j]["minimal"][k]);
                                tempFormattedActions.push(tempObject);
                            }
                        }
                    }

                    // Searches through criticalActions to find out whether any of the standard interventions are missing
                    for (let j = 0; j < criticalActionsResult["criticalActions"].length; j++) {
                        for (let k = 0; k < criticalActionsResult["criticalActions"][j]["standard"].length; k++) {
                            if (!tempConfirmedActions.includes(criticalActionsResult["criticalActions"][j]["standard"][k])) {  // If one of the standard items is missing
                                let tempActionDescription = "N/A";
                                let metaDataEntry = tempMetaData.find((rec) => rec.id === criticalActionsResult["criticalActions"][j]["standard"][k]);
                                if (metaDataEntry == null || metaDataEntry == undefined) {
                                    tempActionDescription = criticalActionsResult["criticalActions"][j]["standard"][k] + " not found";
                                } else {
                                    tempActionDescription = "Did not " + metaDataEntry.label;
                                }
                                let tempObject = {
                                    "id": criticalActionsResult["criticalActions"][j]["standard"][k],
                                    "numericalID": indexCounter,
                                    "status": "missingStandard",
                                    "contraindicatedWhy": "N/A",
                                    "minimalWhy": "N/A",
                                    "label": criticalActionsResult["criticalActions"][j]["label"],
                                    "protocol": criticalActionsResult["criticalActions"][j]["protocol"],
                                    "actionDescription": tempActionDescription,
                                    "timestamp": -1
                                }
                                indexCounter++;
                                tempConfirmedActions.push(criticalActionsResult["criticalActions"][j]["minimal"][k]);
                                tempFormattedActions.push(tempObject);
                            }
                        }
                    }

                    // Checks if any assessments are missing
                    for (let i = 0; i < metaDataResult["CHECKLIST_META_DATA"].length; i++) {
                        if (metaDataResult["CHECKLIST_META_DATA"][i].type === "assessment" && !tempConfirmedActions.includes(metaDataResult["CHECKLIST_META_DATA"][i].id)) {  // If this is an assessment and it is missing
                            let tempActionDescription = "N/A";
                            let metaDataEntry = tempMetaData.find((rec) => rec.id === metaDataResult["CHECKLIST_META_DATA"][i].id);
                            if (metaDataEntry == null || metaDataEntry == undefined) {
                                tempActionDescription = metaDataResult["CHECKLIST_META_DATA"][i].id + " not found";
                            } else {
                                tempActionDescription = "Did not " + metaDataEntry.label;
                            }
                            let tempObject = {
                                "id": metaDataResult["CHECKLIST_META_DATA"][i].id,
                                "numericalID": indexCounter,
                                "status": "missingAssessment",
                                "contraindicatedWhy": "N/A",
                                "minimalWhy": "N/A",
                                "label": "Missing assessment",
                                "protocol": "N/A",
                                "actionDescription": tempActionDescription,
                                "timestamp": -1
                            }
                            indexCounter++;
                            tempConfirmedActions.push(metaDataResult["CHECKLIST_META_DATA"][i].id);
                            tempFormattedActions.push(tempObject);
                        }
                    }

                    // Checks each entry and inserts an appropriate header for each group of entries. The header uses the "worst" color in that group
                    let currentIndex = 0;
                    let criticalActionHeaderIndex = -1;
                    let criticalActionHeaderColor = "";
                    let criticalActionHeaderLabel = "";
                    while (true) {
                        if (currentIndex >= tempFormattedActions.length || tempFormattedActions[currentIndex]["label"] !== criticalActionHeaderLabel) {  // When it gets to a different section
                            let tempHeaderObject = {
                                "id": "criticalActionHeader",
                                "numericalID": -1,
                                "status": criticalActionHeaderColor,
                                "contraindicatedWhy": "N/A",
                                "minimalWhy": "N/A",
                                "label": criticalActionHeaderLabel,
                                "protocol": "N/A",
                                "actionDescription": "N/A",
                                "timestamp": -1
                            }
                            if (criticalActionHeaderIndex !== -1) {
                                tempFormattedActions.splice(criticalActionHeaderIndex, 0, tempHeaderObject);  // Inserts the header in the appropriate section
                                currentIndex++;  // To account for the inserted element
                            }
                            if (currentIndex >= tempFormattedActions.length) {
                                break;
                            }
                            criticalActionHeaderIndex = currentIndex;
                            criticalActionHeaderColor = "green";
                            criticalActionHeaderLabel = tempFormattedActions[currentIndex]["label"];
                        }
                        if (tempFormattedActions[currentIndex]["status"] === "contraindicated") {
                            criticalActionHeaderColor = "red";
                        }
                        if (tempFormattedActions[currentIndex]["status"] === "missingMinimal") {
                            criticalActionHeaderColor = "red";
                        }
                        if (tempFormattedActions[currentIndex]["status"] === "missingAssessment") {
                            criticalActionHeaderColor = "red";
                        }
                        if (tempFormattedActions[currentIndex]["status"] === "missingStandard" && criticalActionHeaderColor !== "red") {  // Red has a higher priority than yellow
                            criticalActionHeaderColor = "yellow";
                        }
                        currentIndex++;
                    }

                    setFormattedActions(tempFormattedActions);
                })
                .catch((err) => console.log(err))
                : setCurrentDocument([]);

            }).catch((err) => console.log(err))

        }).catch(() => console.log('Error retrieving metadata') )

        getCollection('logs')
        .then((result) => {
            setLogs(result); // Load logs from db
        })
        .catch(() => console.log('Document loaded error') )

        listDocumentNames('logs').then((result) => {
            setLogsList(result); // Load logs list from db
        }).catch(() => console.log('Document loaded error') )

    }, [documentID]);

    return (
        <>
            <Header />
            <Button
            color="primary"
            onClick={toggleCommentColumn}>
            {(showCommentColumn) ? "Hide comment column" : "Show comment column"}
            </Button>
            {/* Creates the buttons that are used to switch between documents */}
            <div>
            {logsList ? Object.keys(logsList).map(function(key, index) {
                return (
                    <>
                    <Button
                    color="secondary"
                    onClick={() => setDocumentID(key)}>
                    { logsList[key]["label"] }
                    </Button>
                    </>
                );
            }) : "Document list has not loaded yet"}
            </div>

            {/* Displays information about the current document */}
            { logsList && (documentID in logsList) ?
                <>
                <h3>Current Document ID: { documentID }</h3>
                <h3>Current Document: {logsList[documentID]["label"]}</h3>
                </>
                : ""
            }

            {/* Sections that displays the log */}
            <div style={{overflowY: "auto", maxHeight: "700px"}}>
                <Table borderless size="md">
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Timestamp</th>
                            <th>Action Description</th>
                            <th>Feedback</th>
                            {(showCommentColumn) ? <th>Comments</th> : ""}
                        </tr>
                    </thead>
                    <tbody>
                        {/* Actions the student performed along with missing actions*/}
                        {(formattedActions) ? formattedActions.map(function(currentEntry, index) {
                            return (
                                <>
                                    {/* Displays in a different format if the current entry is a header */}
                                    {(currentEntry.id === "criticalActionHeader") ?
                                    <tr>
                                        <th scope="row" className={getColor(currentEntry)}>{currentEntry.label}</th> {/* Displays the header in the appropriate color and the other columns are blank*/}
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        {(showCommentColumn) ? <td><Input></Input></td> : ""}
                                    </tr>
                                    :
                                    <tr>
                                        <th scope="row">{ currentEntry.numericalID }</th> {/* ID column */}
                                        <td>{getTimestamp(currentEntry)}</td> {/* Converts the timestamp in seconds to a more readable format */}
                                        <td className={getColor(currentEntry)}><span className="material-icons-outlined">{getAssessmentIcon(currentEntry)}</span>{currentEntry.actionDescription}</td> {/* Displays the action description with a color and an icon*/}
                                        <td>{getFeedback(currentEntry)}</td> {/* Feedback column */}
                                        {(showCommentColumn) ? <td><Input></Input></td> : ""}
                                    </tr>
                                    }
                                </>
                            );
                        }) : "Has not loaded yet"}
                    </tbody>
                </Table>
            </div>
        </>
    )
}

export default DebriefingLog;