import { db } from "@meddbriefer/mdb-firebase";
import { getCriticalActions, getMetaData } from "@meddbriefer/scenario-data";

// Check if a document with the given name already exists
export const verifyDocument = async (collectionId, documentName) => {
    let document = await db.collection(collectionId).doc(documentName).get();
    return document.data() != null;
};

// Upload document object to database
export const publishDocument = async (collectionId, documentName, documentObject) => {
    await db.collection(collectionId).doc(documentName).set(documentObject);
    console.log(`Successfully published '${documentName}'.`);
};

// Pull document from database as object
export const getDocument = async (collectionId, documentName) => {
    let document = await db.collection(collectionId).doc(documentName).get();
    return document.data();
};

// Pull collection from database as array of document objects
export const getCollection = async (collectionId) => {
    let collection = await db.collection(collectionId).get();
    return collection.docs.map(item => item.data());
};

// Permanently delete document
export const deleteDocument = async (collectionId, documentName) => {
    await db.collection(collectionId).doc(documentName).delete();
    console.log(`'${documentName}' permanently deleted.`);
    return true;
};

// Get a list of document names and a copy of each document
export const listDocumentNames = async (collectionId) => {
  let documentList = db.collectionGroup(collectionId);
  let documentDictionary = {};
  documentList.get().then((querySnapshot) => {
      querySnapshot.forEach((doc) => {
          documentDictionary[doc.id] = doc.data();
      });
  });
  return documentDictionary;
}

// TODO: May need to update this when more data is moved to the database
export const retrieveCriticalActions = async (scenario, schemaVersion) => {
  return getCriticalActions(db, scenario, schemaVersion);
}

// TODO: May need to update this when more data is moved to the database
export const retrieveMetaData = async (scenarioType, schemaVersion) => {
  return getMetaData(db, scenarioType, schemaVersion)
}